@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Roboto:wght@300;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  font-family: Roboto, sans-serif;
  text-align: center;
  display: grid;
  grid-template-rows: 40vh auto 1fr 8vh;
  background: rgb(248, 201, 179);
}

.App .reasons {
  color: rgba(0, 0, 0, 0.7);
  align-self: center;
  text-align: left;
  padding: 20px 20px 10px 20px;
  font-family: Montserrat, sans-serif;
}

@media only screen and (max-width: 600px) {
  .App .reasons {
    font-size: 0.9em;
  }
}

.header-container {
  display: grid;
  grid-template: 1fr 1fr / 1.5fr 1fr;
  height: 40vh;
  background: #b3a684;
}

.header-container .image-wrapper {
  width: 106vw;
  height: 100%;
  grid-area: 1 / 1 / -1 / -1;
  -webkit-filter: sepia(0.9);
          filter: sepia(0.9);
  transition: -webkit-filter ease-in 1.5s;
  transition: filter ease-in 1.5s;
  transition: filter ease-in 1.5s, -webkit-filter ease-in 1.5s;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-column-gap: 0;
  -webkit-column-gap: 0;
          column-gap: 0;
}

.header-container:hover .image-wrapper {
  -webkit-filter: unset;
          filter: unset;
}

.header-container .clip {
  grid-area: 1 / 1 / -1 / -1;
  background: #000;
  height: 100%;
  width: 100%;
  z-index: 50;
  opacity: 0.5;
  -webkit-clip-path: circle(150vw at 120% 100%);
          clip-path: circle(150vw at 120% 100%);
  transition: opacity ease-out 1s, -webkit-clip-path linear 1s;
  transition: clip-path linear 1s, opacity ease-out 1s;
  transition: clip-path linear 1s, opacity ease-out 1s, -webkit-clip-path linear 1s;
}

.header-container:hover .clip {
  -webkit-clip-path: circle(10vw at 120% 50%);
          clip-path: circle(10vw at 120% 50%);
  opacity: 0;
  transition: opacity ease-out 1s, -webkit-clip-path linear 1s;
  transition: clip-path linear 1s, opacity ease-out 1s;
  transition: clip-path linear 1s, opacity ease-out 1s, -webkit-clip-path linear 1s;
}

.header-container .text-wrapper {
  width: 100%;
  height: 40vh;
  grid-area: 1 / 2 / -1 / -1;
  z-index: 100;

  display: grid;
  grid-template: 2fr 1fr / 1fr;

  text-align: left;
  font-family: Montserrat, sans-serif;
  color: #fff;
}

.header-container .image-wrapper .image-container {
  height: 100%;
  width: 100%;
  transform: skewX(-7deg) translateX(-3vw);
  overflow: hidden;
  position: relative;
}

.header-container .image-container img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) skewX(7deg) scale(0.8);
}

.header-container .image-container img[alt="mumbai-3"] {
  transform: translate(-40%, -70%) skewX(7deg);
}

.header-container .text-wrapper h1 {
  align-self: end;
  font-size: 7em;
  transition: color ease-out 2s;
}

.header-container .text-wrapper h2 {
  font-size: 3em;
  position: relative;
}

.header-container:hover .text-wrapper h2 {
  background: radial-gradient(
    rgb(255, 72, 0),
    rgb(253, 253, 252),
    rgb(63, 221, 0),
    rgb(255, 255, 255)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.header-container:hover .text-wrapper h1 {
  background: linear-gradient(
    135deg,
    rgb(255, 72, 0),
    rgb(253, 253, 252),
    rgb(63, 221, 0),
    rgb(255, 255, 255)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media only screen and (max-width: 1250px) {
  .header-container {
    grid-template: 1fr 1fr / 1fr 1fr;
  }

  .header-container .image-wrapper .image-container:nth-of-type(1),
  .header-container .image-wrapper .image-container:nth-of-type(5) {
    display: none;
  }

  .header-container .text-wrapper h1 {
    font-size: 5em;
  }
  .header-container .text-wrapper h2 {
    font-size: 2em;
  }
}

@media only screen and (max-width: 750px) {
  .header-container {
    grid-template: 1fr 1fr / 0.5fr 1fr;
  }

  .header-container .image-wrapper .image-container {
    transform: skewX(-7deg) translateX(-3vw);
  }

  .header-container .image-wrapper .image-container:nth-of-type(2),
  .header-container .image-wrapper .image-container:nth-of-type(4) {
    display: none;
  }

  .header-container .image-container img[alt="mumbai-3"] {
    transform: translate(-50%, -70%) skewX(7deg) scale(1.14);
  }
}

@media only screen and (max-width: 550px) {
  .header-container {
    grid-template: 1fr 1fr / 0.2fr 1fr;
  }

  .header-container .image-container img[alt="mumbai-3"] {
    transform: translate(-50%, -70%) skewX(7deg) scale(1.2);
  }

  .header-container .text-wrapper h1 {
    font-size: 4.5em;
  }
  .header-container .text-wrapper h2 {
    font-size: 1.7em;
  }
}

.main-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  grid-gap: 10px;
  gap: 10px;
  margin: 10px;
}

.card-container {
  background: rgba(83, 63, 42, 0.856);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 2px 2px rgba(66, 34, 7, 0.3);
  transition: transform ease-in 0.2s;

  display: grid;
  grid-template-rows: 300px 50px 1fr;
}

.card-container:hover {
  transform: scale(1.02);
}

.card-container .clip {
  grid-area: 1 / 1 / -1 / -1;
  background: linear-gradient(
    135deg,
    rgb(247, 96, 171),
    rgb(182, 28, 174),
    rgb(88, 20, 134),
    rgb(69, 21, 180)
  );
  z-index: 1;
  -webkit-clip-path: circle(0% at 0% 0%);
          clip-path: circle(0% at 0% 0%);
  transition: -webkit-clip-path ease-out 0.5s;
  transition: clip-path ease-out 0.5s;
  transition: clip-path ease-out 0.5s, -webkit-clip-path ease-out 0.5s;
}

.card-container:hover .clip {
  -webkit-clip-path: circle(200% at 0% 0%);
          clip-path: circle(200% at 0% 0%);
}

.card-container:hover .card-showcase {
  -webkit-filter: unset;
          filter: unset;
  transform: scale(1.1);
}

.card-container .card-showcase {
  align-self: center;
  justify-self: center;
  border-radius: 50%;
  overflow: hidden;
  height: 230px;
  width: 230px;
  -webkit-filter: sepia(0.9);
          filter: sepia(0.9);
  grid-area: 1 / 1 / 2 / 2;
  z-index: 2;
  transition: transform ease-out 0.5s, -webkit-filter ease-out 0.5s;
  transition: filter ease-out 0.5s, transform ease-out 0.5s;
  transition: filter ease-out 0.5s, transform ease-out 0.5s, -webkit-filter ease-out 0.5s;

  display: flex;
  align-items: center;
  justify-content: center;
}

.card-showcase img {
  height: 230px;
  object-fit: cover;
}

.card-container .card-title {
  align-self: center;
  justify-self: center;
  font-size: 2em;
  font-family: Montserrat, sans-serif;
  padding: 0 10px;
  color: #fff;
  grid-area: 2 / 1 / 3 / 2;
  z-index: 3;
}

.card-container .card-body {
  align-self: start;
  justify-self: center;
  font-family: Montserrat, sans-serif;
  padding: 20px 50px 40px 50px;
  text-align: left;
  letter-spacing: 0.8px;
  line-height: 1.4;
  color: #fff;
  grid-area: 3 / 1 / 4 / 2;
  z-index: 4;
}

.footer-container {
  height: 100%;
  display: grid;
  grid-template: 0.5fr 1fr / 1fr;
  align-items: center;
  justify-content: center;
  font-family: Montserrat, sans-serif;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.5);
  padding: 10px;
}

.footer-container p:nth-of-type(2) {
  font-size: 1.5em;
}

